<template>
  <v-card flat tile>
    <v-toolbar dense>
      <v-toolbar-title class="body-2">Node A</v-toolbar-title>
      <v-spacer/>
    </v-toolbar>
    <node-form v-if="a != undefined" :item="a" />
    <v-toolbar dense>
      <v-toolbar-title class="body-2">Node B</v-toolbar-title>
      <v-spacer/>
    </v-toolbar>
    <node-form v-if="b != undefined" :item="b" />
    <v-divider/>
    <v-card-text class="py-0">
      <v-text-field type="number" label="Jarak" flat :value="length" readonly />
    </v-card-text>
    <v-divider/>
    <v-card-actions>
      <v-btn text dark color="blue" @click="save">Save</v-btn>
      <v-btn text dark color="pink" @click="$emit('reset')">Reset</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import NodeForm from "./NodeForm.comp";

export default {
  name: "RouteForm",
  components: {
    NodeForm,
  },
  props: ["a", "b", "length"],
  methods: {
    save() {
      if (this.a == undefined || this.b == undefined) {
        this.$emit("invalid");
      }
      const payload = {
        a: this.a,
        b: this.b,
        length: this.length
      };
      this.$emit("save", payload);
    }
  }
}
</script>

