<template>
  <div id="jalurd3">
    <v-navigation-drawer dark app id="jalurd3-nav">
      <v-toolbar text dark>
        <v-toolbar-title class="subtitle">Editor Peta</v-toolbar-title>
        <v-spacer/>
        <v-toolbar-items>
          <v-btn icon to="/admin/setup">
            <v-icon>mdi-rocket</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-layout align-center justify-center class="mb-0">
        <v-btn-toggle v-model="mode" light rounded color="green darken-1" class="my-3" mandatory>
          <v-btn value="view">
            <v-icon small>mdi-chart-timeline-variant</v-icon>
          </v-btn>
          <v-btn value="create-route">
            <v-icon small>mdi-chart-timeline-variant</v-icon>
          </v-btn>
          <v-btn value="create-intersection">
            <v-icon small>mdi-star-three-points</v-icon>
          </v-btn>
        </v-btn-toggle>
      </v-layout>

      <div class="body-2 text-xs-center pa-4 py-0 white--text font-weight-bold">
        {{ currentModeTitle }}
      </div>

      <div v-if="mode == 'view'">
        <div v-if="viewModeType == 'node'" class="grey darken-3 white--text">
          <node-form :item="node"/>
          <v-btn @click="deleteIntersection" dark outlined small color="pink" class="ml-4 my-2">delete</v-btn>
        </div>
        <div v-if="viewModeType == 'edge'" class="pl-3 grey darken-3 white--text py-2">
          <div class="body-2">Start</div>
          <div class="pl-4 py-2">
            <div class="caption mb-2">Lat: {{ edge.a.latitde }}</div>
            <div class="caption mb-2">Lng: {{ edge.a.longitude }}</div>
          </div>
          <div class="body-2">End</div>
          <div class="pl-4 py-2">
            <div class="caption mb-2">Lat: {{ edge.b.latitde }}</div>
            <div class="caption mb-2">Lng: {{ edge.b.longitude }}</div>
          </div>
          <div class="body-2 mb-2">Jarak: {{ edge.length }}m</div>
          <v-btn @click="deleteRoute" dark outlined small color="red">Hapus</v-btn>
        </div>
      </div>

      <div id="create-route" v-if="mode == 'create-route'">
        <route-form
          :a="routes.a"
          :b="routes.b"
          :length="routes.length"
          @save="addRoute"
          @reset="resetCreateNodeForm"
        />
      </div>

      <div v-if="mode == 'create-intersection'" class="grey darken-4 white--text">
        <div class="py-2 pl-4">
          <div class="caption my-2">Lat: {{ newIntersection.latitude }}</div>
          <div class="caption">Lng: {{ newIntersection.longitude }}</div>
          <v-btn dark outlined small class="mt-2" @click="addIntersection">Save</v-btn>
        </div>
      </div>

    </v-navigation-drawer>

    <div id="map-container" style="height: 100vh;">
      <div id="jalurd3-gmap" style="height: 100%;"></div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { node as nodeRepo, edge as edgeRepo } from "@/services/repo";
import { CustomMap, getDistance } from "@/services/lmapd3";
import { node as defaultNode, edge as defaultEdge } from "@/services/defaults";
import RouteForm from "./RouteForm.comp";
import NodeForm from "./NodeForm.comp";

export default {
  name: "JalurD3",

  components: {
    NodeForm,
    RouteForm,
  },

  // Store reference to current map.
  map: undefined,

  data: () => ({
    items: [],
    edges: [],
    routes: {
      a: undefined,
      b: undefined,
      length: 0
    },

    mode: "view",
    viewModeType: "node",

    // Data for rt/rw mode.
    node: defaultNode(),
    edge: defaultEdge(),

    newIntersection: {
      latitude: 0,
      longitude: 0
    }
  }),

  methods: {
    async reload() {
      let nodes = await nodeRepo.find({ jabatan: "all" });
      let intersections = await nodeRepo.find({ jabatan: "intersection" })
      let edges = await edgeRepo.find({});
      let allNodes = [...nodes, ...intersections];

      edges.forEach(e => {
        console.log(e);
      });

      this.map.removeAll();
      this.map.addEdges(edges);
      this.map.addNodes(allNodes);
    },

    addNodeToRoutes(n) {
      if (this.routes.a == undefined) {
        this.routes.a = {...n};
      } else if (this.routes.b == undefined) {
        this.routes.b = {...n};
        // There is two valid points.
        // Calculate the distance.
        let origin = [ this.routes.a.latitude, this.routes.a.longitude ];
        let destination = [ this.routes.b.latitude, this.routes.b.longitude ];
        let d = getDistance(origin, destination);
        this.routes.length = d;
      }
    },
    async addRoute(edge) {
      // Construct payload
      const payload = {
        aId: edge.a._id,
        bId: edge.b._id,
        a: {
          latitude: edge.a.latitude,
          longitude: edge.a.longitude,
        },
        b: {
          latitude: edge.b.latitude,
          longitude: edge.b.longitude,
        },
        length: edge.length
      };
      try {
        await edgeRepo.create(payload);
      } finally {
        this.resetCreateNodeForm();
        this.reload();
      }
    },
    resetCreateNodeForm() {
      this.routes.a = undefined;
      this.routes.b = undefined;
    },
    async deleteRoute() {
      try {
        await edgeRepo.delete({ id: this.edge._id });
      } finally {
        this.reload();
      }
    },

    addIntersection() {
      const point = [ this.newIntersection.latitude, this.newIntersection.longitude ];
      nodeRepo.createIntersection({ point })
        .then(() => this.reload());
    },
    async deleteIntersection() {
      try {
        await nodeRepo.delete({ id: this.node._id });
      } finally {
        this.reload();
      }
    }
  },
  computed: {
    currentModeTitle() {
      switch (this.mode) {
        case 'view':
          return 'Viewer';
        case 'create-intersection':
          return 'Buat Interseksi';
        case 'create-route':
          return 'Buat Rute';
        default:
          return '';
      }
    }
  },
  mounted() {
    this.map = new CustomMap();
    this.map.init("jalurd3-gmap");

    // Here we catch the click event in svg elements (circle, line)
    this.map.addSvgListener(d => {
      const mode = this.mode;
      switch (mode) {
        case "view":
          // It could be node (rt, rw, intersection)
          if (d.jabatan != undefined) {
            // Set the view mode accordingly
            this.viewModeType = "node";
            this.node = {...d};
          // Or it is edge.
          } else {
            this.viewModeType = "edge";
            console.log(d);
            this.edge = {...d};
          }
          break;

        case "create-route":
          this.addNodeToRoutes(d);
          break;
          
        default:
          break;
      }
    });

    // The listener only active when we're in create-intersection mode.
    // Because we need to get the lat and lang directly from leaflet.
    this.map.addLeafletListener(d => {
      if (this.mode != 'create-intersection') {
        return;
      }
      this.newIntersection.latitude = d.latlng.lat;
      this.newIntersection.longitude = d.latlng.lng;
    });

    this.reload();
  }
}
</script>

<style lang="scss">
  .node-circle {
    pointer-events: auto;
    cursor: pointer;
  }
  .jalur-d3-line {
    pointer-events: auto;
    cursor: pointer;
  }
</style>

